import { useState } from 'react';
import {Link, useNavigate} from 'react-router-dom'
import AuthInput from '../../components/Auth/AuthInput/AuthInput.jsx'
import AuthPageContainer from '../../components/Auth/AuthPageContainer/AuthPageContainer.jsx'
import Button from '../../components/Button/Button.jsx'
import {userSignup} from '../../api/user.js'
import {Toast} from '../../utility/helper.js'
import styles from './SignUpPage.module.scss'

export default function SignUpPage () {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (name.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (email.trim().length === 0) {
      Toast.fire({
        title: "email!",
        icon: "error",
      });
      return;
    }
    if (password.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (checkPassword.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (checkPassword !== password) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (!email.includes("@")) {
      Toast.fire({
        title: "email!",
        icon: "error",
      });
      return;
    }
    const data = await userSignup({
      name,
      account,
      email,
      password,
      checkPassword,
    });
    // signup success
    if (data==="") {
      Toast.fire({
        title: "",
        icon: "success",
      });
      navigate("/login");
      return;
    // signup fail
    } 
    
    Toast.fire({
      title: data.response.data.message,
      icon: "error",
    });

  }

  return (
    <AuthPageContainer title=''>
      <AuthInput label='' value={account} placeholder='' onChange={(accountInputValue) => setAccount(accountInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='' value={name} placeholder='' onChange={(nameInputValue) => setName(nameInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='Email' value={email} placeholder='Email' onChange={(emailInputValue) => setEmail(emailInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='' type='password' value={password} placeholder='' onChange={(passwordInputValue) => setPassword(passwordInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='' type='password' value={checkPassword} placeholder='' onChange={(checkPasswordInputValue) => setCheckPassword(checkPasswordInputValue)}
      notification='!' wordsLimit={20}
      />
      <Button title='' size='large' isAction onClick={handleClick}></Button>
      <div className={styles.link} >
        <Link to="/login">
          <span className={styles.span}></span>
        </Link>
      </div>
    </AuthPageContainer>
  );
}